.page-main-header--catalog {
  background-color: @special-dark;
  border-radius: 0 0 20px 20px;

  @media (min-width: @tablet-width) {
    border-radius: 0 0 60px 60px;
  }

  @media (min-width: @desktop-width) {
    border-radius: 0 0 56px 56px;
  }
}

.page-main-header--form {
  @media (min-width: @desktop-width) {
    &::before {
      position: absolute;
      top: -90px;

      width: 100%;
      height: 200px;
      content: "";

      background-color: @special-dark;
    }
  }
}

.page-main-header__user-profile {
  padding: 20px 0 34px;
}

.page-main-header__container {
  display: grid;
}

.page-main-header__container--catalog {
  grid-template-columns: 1fr auto;
  column-gap: 50px;
  align-items: center;

  @media (min-width: @tablet-width) {
    padding: 0 34px;
  }

  @media (min-width: @desktop-width) {
    padding: 0 66px;
  }
}

.page-main-header__container--form {
  position: relative;
  grid-template-columns: 1fr 135px 135px 1fr;
  padding-bottom: 64px;

  &::before,
  &::after {
    position: relative;
    grid-column: 1 / -1;
    grid-row: 1 / 2;
    content: "";
  }

  &::before {
    top: 4px;
    background-color: @basic-yellow;
  }

  &::after {
    background-color: @special-dark;
    border-radius: 0 0 20px 20px;
  }

  @media (min-width: @tablet-width) {
    grid-template-columns: 1fr 94px 60px 308px 60px 94px 1fr;
    padding-bottom: 112px;

    &::before {
      display: none;
    }

    &::after {
      border-radius: 0 0 60px 60px;
    }
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: 1fr 64px 732px 100px 95px 44px 190px 95px 1fr;
    padding-bottom: 117px;

    &::after {
      border-radius: 0 0 56px 56px;
    }
  }
}

.page-main-header__title {
  position: relative;
  max-width: 270px;
  margin: 0;
  margin-right: auto;
  overflow-wrap: break-word;

  font-size: 36px;
  line-height: 36px;
  font-weight: 700;
  color: @basic-white;

  &::after {
    position: absolute;
    right: 0;
    bottom: -16px;
    left: 0;

    height: 5px;
    content: "";

    background-color: @special-orange;
    border-radius: 4px;
  }

  @media (min-width: @tablet-width) {
    max-width: 380px;
    font-size: 60px;
    line-height: 60px;

    &::after {
      bottom: -76px;

      height: 8px;
    }
  }

  @media (min-width: @desktop-width) {
    max-width: 450px;
  }
}

.page-main-header__title--form {
  position: relative;
  z-index: 1;
  grid-column: 2 / -2;
  grid-row: 1 / 2;
  padding: 57px 0 15px;

  &::after {
    bottom: -2px;
  }

  @media (min-width: @tablet-width) {
    padding: 91px 0 69px;

    &::after {
      bottom: -4px;
    }
  }

  @media (min-width: @desktop-width) {
    padding: 81px 0 71px;
    grid-column: 3 / 4;
  }
}

.page-main-header__index-link {
  display: none;
  padding-top: 9px;
  align-items: center;

  font-family: @general-font-family;
  font-size: 20px;
  line-height: 20px;
  font-weight: 700;
  color: @basic-white;
  text-decoration: none;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    display: grid;
    max-width: 500px;
    grid-template-columns: 1fr auto;
    column-gap: 28px;

    &::after {
      grid-column: 2 / 3;

      width: 32px;
      height: 16px;

      content: "";

      background: transparent url("../img/icons/arrow-bend.svg") no-repeat center;
      background-size: contain;
    }

    &:hover,
    &:focus {
      opacity: 0.6;
    }

    &:focus:not(:focus-visible) {
      opacity: 1;
    }

    &:focus:not(:focus-visible):hover {
      opacity: 0.6;
    }

    &:focus:not(:focus-visible):active {
      opacity: 0.3;
    }

    &:focus-visible {
      opacity: 0.6;
      outline: none;
    }

    &:active {
      opacity: 0.3;
    }
  }
}
