*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: none;
  white-space: nowrap;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
  appearance: none;
}
.container {
  width: calc(320px - 50px);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: calc(768px - 80px);
  }
}
@media (min-width: 1440px) {
  .container {
    width: calc(1440px - 120px);
  }
}
.button {
  display: grid;
  align-items: center;
  justify-items: center;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-style: normal;
  text-decoration: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.button--primary {
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  color: #161c35;
  text-transform: uppercase;
  background-color: #ffd74b;
  border-radius: 25px;
}
@media (min-width: 768px) {
  .button--primary {
    font-size: 20px;
  }
}
.button--iconic {
  border-radius: 50%;
}
.button--secondary {
  justify-items: start;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
  color: #1d2e5b;
  text-transform: none;
  background-color: #ffd74b;
  border-radius: 10px;
}
.button--secondary::after {
  grid-column: 2 / 3;
  justify-self: end;
  content: "";
  border-top: 5px solid transparent;
  border-right: none;
  border-bottom: 5px solid transparent;
  border-left: 10px solid #1d2e5b;
}
@media (min-width: 768px) {
  .button--secondary {
    font-size: 24px;
    line-height: 24px;
    border-radius: 20px;
  }
}
@media (min-width: 1440px) {
  .button--secondary {
    font-size: 24px;
    line-height: 24px;
  }
}
.button--secondary:hover,
.button--secondary:focus {
  background-color: #ffbd30;
}
.button--secondary:focus:not(:focus-visible) {
  background-color: #ffd74b;
}
.button--secondary:focus:not(:focus-visible):hover {
  background-color: #ffbd30;
}
.button--secondary:focus:not(:focus-visible):active {
  color: rgba(29, 46, 91, 0.3);
  background-color: #ffbd30;
}
.button--secondary:focus:not(:focus-visible):active::after {
  border-left-color: rgba(29, 46, 91, 0.3);
}
.button--secondary:focus-visible {
  background-color: #ffbd30;
  outline: none;
}
.button--secondary:active {
  color: rgba(29, 46, 91, 0.3);
  background-color: #ffbd30;
}
.button--secondary:active::after {
  border-left-color: rgba(29, 46, 91, 0.3);
}
.button--special {
  justify-items: start;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
  color: #1d2e5b;
  text-transform: none;
  background-color: #ffd74b;
  border: 3px solid #192144;
  border-radius: 10px;
}
.button--special::after {
  grid-column: 2 / 3;
  justify-self: end;
  content: "";
  border-top: 10px solid transparent;
  border-right: none;
  border-bottom: 10px solid transparent;
  border-left: 16px solid #1d2e5b;
}
@media (min-width: 768px) {
  .button--special {
    border-radius: 20px;
  }
}
@media (min-width: 1440px) {
  .button--special {
    font-size: 24px;
    line-height: 24px;
  }
}
.button--special:hover,
.button--special:focus {
  color: #ffffff;
  background-color: #192144;
}
.button--special:hover::after,
.button--special:focus::after {
  border-left-color: #ffffff;
}
.button--special:focus:not(:focus-visible) {
  color: #192144;
  background-color: transparent;
}
.button--special:focus:not(:focus-visible)::after {
  border-left-color: #192144;
}
.button--special:focus:not(:focus-visible):hover {
  color: #ffffff;
  background-color: #192144;
}
.button--special:focus:not(:focus-visible):hover::after {
  border-left-color: #ffffff;
}
.button--special:focus:not(:focus-visible):active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #192144;
}
.button--special:focus:not(:focus-visible):active::after {
  border-left-color: rgba(255, 255, 255, 0.3);
}
.button--special:focus-visible {
  color: #ffffff;
  background-color: #192144;
  outline: none;
}
.button--special:focus-visible::after {
  border-left-color: #ffffff;
}
.button--special:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #192144;
}
.button--special:active::after {
  border-left-color: rgba(255, 255, 255, 0.3);
}
.button--social {
  width: 45px;
  height: 45px;
  background-color: #ffd74b;
  background-position: center;
}
.button--social .social__icon {
  fill: #192144;
}
@media (min-width: 768px) {
  .button--social {
    width: 62px;
    height: 62px;
  }
}
.button--social:hover,
.button--social:focus {
  background-color: #ffbd30;
}
.button--social:focus:not(:focus-visible) {
  background-color: #ffd74b;
}
.button--social:focus:not(:focus-visible):hover {
  background-color: #ffbd30;
}
.button--social:focus:not(:focus-visible):active .social__icon {
  fill: rgba(25, 33, 68, 0.3);
}
.button--social:focus-visible {
  background-color: #ffbd30;
  outline: none;
}
.button--social:active .social__icon {
  fill: rgba(25, 33, 68, 0.3);
}
.button--social-contour {
  width: 45px;
  height: 45px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .button--social-contour {
    width: 60px;
    height: 60px;
  }
}
.button--social-contour:hover,
.button--social-contour:focus {
  opacity: 0.6;
}
.button--social-contour:focus:not(:focus-visible) {
  opacity: 1;
}
.button--social-contour:focus:not(:focus-visible):hover {
  opacity: 0.6;
}
.button--social-contour:focus:not(:focus-visible):active {
  opacity: 0.3;
}
.button--social-contour:focus-visible {
  opacity: 0.6;
  outline: none;
}
.button--social-contour:active {
  opacity: 0.3;
}
.button--facebook {
  background-image: url("../img/icons/social/facebook.svg");
}
@media (min-width: 768px) {
  .button--facebook {
    background-size: 14px auto;
    background-position: 50% 50%;
  }
}
.button--instagram {
  background-image: url("../img/icons/social/instagram.svg");
}
@media (min-width: 768px) {
  .button--instagram {
    background-size: 24px auto;
    background-position: 53% 58%;
  }
}
.button--vk {
  background-image: url("../img/icons/social/vkontakte.svg");
}
@media (min-width: 768px) {
  .button--vk {
    background-size: 31px auto;
    background-position: 50% 60%;
  }
}
